<template>
	<div class="progress-circle">
	<!-- viewBox 0 0(100, 100) circle 50svg  widthheight  -->
		<svg :width="radius" :height="radius" viewBox="0 0 100 100" version="1.1" xmlns="http://www.w3.org/2000/svg">
		<!-- r cx cy -->
			<circle class="progress-background" r="50" cx="50" cy="50"></circle>
			<!-- stroke-dasharray="100"  stroke-dashoffset percent 0 -->
			<circle class="progress-bar" r="50" cx="50" cy="50" fill="transparent" :stroke-dasharray="dashArray" :stroke-dashoffset="dashOffset"></circle>
		</svg>
		<slot></slot>
	</div>
</template>
<script type="text/ecmascript-6">
export default {
	props: {
		radius: {
			type: Number,
			default: 100
		},
		percent: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			//  50*2*PI
			dashArray: Math.PI * 100
		}
	},
	computed: {
		dashOffset() {
			// percent0  0 stroke-dashoffset360
			return (1 - this.percent) * this.dashArray
		}
	}
}

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';
.progress-circle
	position: relative
	circle
		stroke-width: 8px
		/* stroke-width */
		transform-origin: center
		&.progress-background
			transform: scale(0.9)
			stroke: $color-theme-d
		&.progress-bar
			transform: scale(0.9) rotate(-90deg)
			stroke: $color-theme

</style>



